<span class="fixed-item abs">GUIDO MORAN</span>



<style>

    .layout {
		z-index: 10;
		top: 0;
		left: 0;
		position: fixed;
		width: 100vw;
		height: 100vh;
		color: white;
		font-weight: 600;
		animation: layout-color;
		animation-timeline: scroll(root);
		animation-range: 0px 1500px;
	}

	@keyframes layout-color { from { color: white } to { color: rgb(200, 200, 200) } }
	
	.layout-item:nth-child(1)::before, .layout-item:nth-child(3)::before {
		content: ""; opacity: 0; transition: opacity 1s; 
	}

	.layout-item:nth-child(1):hover::before, .layout-item:nth-child(3):hover::before {
		content: "▲"; position: absolute; top: calc(-1.5rem); left: calc(50% - 8px); opacity: 1;
	}

	.layout-item:nth-child(1) {
		transform: rotateZ(-90deg); left: -2rem; animation: layout-item-2 1s ease-in-out;
		backdrop-filter: blur(5px);
		background-color: rgba(0, 0, 0, 0.1);
	}

	.layout-item:nth-child(3) {
		transform: rotateZ(90deg); right: -1rem; animation: layout-item-3 1s ease-in-out;
		backdrop-filter: blur(5px);
		background-color: rgba(0, 0, 0, 0.1);
	}

	.layout-item:nth-child(1), .layout-item:nth-child(3) {

 		top: 50%; margin: 1.5rem; padding: 0.5rem; 
		animation-fill-mode: forwards;
		animation-delay: 3s;
	}

	.layout-item:nth-child(2) {
		top: 100px; width: 70%; text-align: center; font-size: 5rem;
		animation: layout-item-1 ease-in-out 3s;
		animation-fill-mode: forwards;
		backdrop-filter: blur(5px);

	}

	@keyframes layout-item-1 { 
		0% { width: 45%; font-size: 5rem; top: 100px; }

		33% { width: 45%; font-size: 5rem; top: 100px; }

		99% { opacity: 1 }

		100% { opacity: 0; width: 100%; font-size: 1rem; top: 20px; }
	}

	.layout-item:nth-child(4) {

		bottom: 3rem;
		width: 100%;
		font-size: 2.25rem;
		text-align: center;
	}
	
	.fixed-item {
		z-index: 50;
		opacity: 0;
		position: fixed;
		width: 100vw;
		font-size: 1rem;
		top: 20px;
		text-align: center; 
		animation: fixed-item-1 ease-in-out 3s;
		animation-fill-mode: forwards;
		color: white;
		mix-blend-mode: difference;
		font-weight: 600;

	}

	@keyframes fixed-item-1 { 
		0% { opacity: 0 }

		99% { opacity: 0 }

		100% { opacity: 1 }
	}

	.layout-item:nth-child(5)  {
	
		rotate: 90deg;
		bottom: 5rem;
		right: 5rem;
	
		animation: scale ease-in-out 1s 5;
		animation-delay: 3s;
		animation-fill-mode: forwards;
	}

	@keyframes scale { 
		from { scale: 1 }
		50% { scale: 1.25 }
		to { scale: 1 }
	}

	.layout-item:nth-child(6)  {

		top: 50px;
		right: 50px;
		text-align: center;
		background-color: rgba(250, 250, 250, 0.1);
		padding: 1.25rem;
		padding-top: 0rem;
		border-radius: 10px;

	}

	.layout-item:nth-child(6) > svg {

		position: relative;
		top: 13px;
		left: 5px;

		
	}


</style>
