<script>
	import { onMount } from "svelte";

	let terminalPath = " C:\\Users\\Public>"
	let blinkingBar = "_";
	let terminalWriting = JSON.parse(JSON.stringify(terminalPath));
	let terminalContent;
	let terminalLoading;
	let terminalAttached;
	let terminal;
	let inputterminal;

	const technologies = {
		Languages: [
			"Javascript",
			"Python",
			"Bash",
			"C++"
		],
		Databases: [
			"MongoDB",
			"Redis",
			"Cassandra",
			"SQLite",
			"MySQL"
		],
		"Developing Stack & Tools": [
			"Git | Github",
			"Docker",
			"Insomnia",
			"Linux",
			"Wireshark",
			"NumPy",
			"Svelte",
			"NGINX",
			"VirtualBox | VMWare"
		]
	}
	const history = [];
	const filterKeys = [
		"Control",  "Alt", "CapsLock", "Shift", "Insert", "PageUp", "PageDown", "Home", "End", "Delete", "Tab", "Escape", "AudioVolumeDown", "AudioVolumeUp",
		"F1", "F2", "F3", "F4", "F5", "F6", "F7", "F8", "F9", "F10", "F11", "F12", "ArrowRight", "ArrowDown", "ArrowLeft", "ArrowUp"
	];

	onMount( e => { 
		terminalContent = document.getElementById("terminal-body-content"); 
		terminal = document.getElementById("terminal-body");  
		inputterminal = document.getElementById("input");  

		terminalContent.innerHTML += 
		terminalPath + " help" + "<br>" +
		`HELP <span class="ts">Displays this help message, showing the available commands.</span>` + "<br>" +
		`TECH <span class="ts">Displays a list of technologies frecuently used by root user.</span>` + "<br>" +
		`APPS <span class="ts">Displays a list of apps or projects created by root user.</span>` + "<br>" +
		`SYSTEMINFO <span class="ts">Show information about your system.</span>` + "<br>" +
		`ECHO <span class="ts">Show messages.</span>` + "<br>" +
		`HISTORY <span class="ts">Display a list of the commands in cache.</span>` + "<br>" +
		// `SHUTDOWN <span class="ts">Allows the local or remote shutdown of a computer.</span>` + "<br>" +
		`CLS <span class="ts">Clears the console.</span>` + "<br>" + "<br>";

		terminal.addEventListener("scroll", _ => inputterminal.style.minHeight = terminal.scrollHeight + 'px' )
	
	});

	window.addEventListener("scroll", e => {
		terminalAttached = false;
	});

	setInterval( _ => {

		blinkingBar = "  ";
		setTimeout( _ => blinkingBar = "_", 300);

	}, 1000);
	
	const manageKey = async e => {

		const key = e.key;

		if ( !filterKeys.includes( key )) {
			e.preventDefault();
			if ( !terminalLoading ) {

				switch ( key ) {

					case "Backspace":
						terminalWriting = terminalWriting.length > terminalPath.length ? terminalWriting.slice(0, -1) : terminalWriting;
						break;

					case "Enter":
						terminalLoading = true;
						terminal.scrollTop = terminal.scrollHeight;
						terminalAttached = true;

						terminalContent.innerHTML += terminalWriting + "<br>";
						const tempWrited = terminalWriting;
						terminalWriting = "";
						await manageCMD(tempWrited);
						terminalWriting = terminalPath;

						if (terminalAttached) { terminal.scrollTop = terminal.scrollHeight + 1000 };
						
						terminalLoading = false;
						break;

					default: 
						terminalWriting = terminalWriting + key;
				}
				
			};
		};

	};

	const manageCMD = async writed => {
		
		writed = writed.slice( terminalPath.length, writed.length );

		switch ( writed.toLocaleLowerCase().split(" ")[0] ) {

			case "":
				terminalContent.innerHTML += "<br>";
				break;

			case "cls":
				history.push( writed.toLocaleUpperCase() );
				inputterminal.style.minHeight = "100%";
				terminalContent.innerHTML = "";
				break;

			case "help":
				history.push( writed.toLocaleUpperCase() );

				terminalContent.innerHTML += 
					`HELP <span class="ts">Displays this help message, showing the available commands.</span>` + "<br>" +
					`TECH <span class="ts">Displays a list of technologies frecuently used by root user.</span>` + "<br>" +
					`APPS <span class="ts">Displays a list of apps or projects created by root user.</span>` + "<br>" +
					`SYSTEMINFO <span class="ts">Show information about your system.</span>` + "<br>" +
					`ECHO <span class="ts">Show messages.</span>` + "<br>" +
					`HISTORY <span class="ts">Display a list of the commands in cache.</span>` + "<br>" +
					// `SHUTDOWN <span class="ts">Allows the local or remote shutdown of a computer.</span>` + "<br>" +
					`CLS <span class="ts">Clears the console.</span>` + "<br>" + "<br>";
				break;

			case "systeminfo":
				history.push( writed.toLocaleUpperCase() );
				terminalContent.innerHTML += navigator.userAgent + "<br>" + "<br>";
				break;

			case "tech":
				history.push( writed.toLocaleUpperCase() );

				for (const k of Object.keys(technologies)) {

					terminalContent.innerHTML += `${k}<br>`;

					for (const t of technologies[k]) {
						await new Promise(resolve => 
							setTimeout( _ => {
								terminalContent.innerHTML += `<span class="ts">${t}</span><br>`;
								if (terminalAttached) terminal.scrollTop = terminal.scrollHeight;
								resolve();
							}, 200)
						)				
					};

					await new Promise(resolve => setTimeout( resolve, 600 ) );	
					terminalContent.innerHTML += "<br>";
					
				};
				break;

			case "apps":

				history.push( writed.toLocaleUpperCase() )

				if(writed.slice(0, 11) === "apps --open") {

					open("https://github.com/Guidoow");
					terminalContent.innerHTML += "<br>";
					break;

				} else {

					terminalContent.innerHTML += "Retrieving data from C: &nbsp;";

					const interval = setInterval( () => {
						terminalContent.innerHTML += ".";
						if (terminalAttached) terminal.scrollTop = terminal.scrollHeight;
					}, 600);

					await new Promise(resolve => 
						setTimeout( async _ => {
							clearInterval(interval);
							terminalContent.innerHTML += "<br>" + "<br>";
							if (terminalAttached) terminal.scrollTop = terminal.scrollHeight;
							resolve();
						}, 2000 )
					)

					await new Promise(resolve => 
						setTimeout( async  _ => {
							const apps = await fetch("https://api.github.com/users/Guidoow/repos").then( res => res.json() ).catch( err => 0);
							if ( apps ) {
								terminalContent.innerHTML = terminalContent.innerHTML.slice(0, -41);

								terminalContent.innerHTML += `use '--open' argument to open the website. <br><br>`;

								terminalContent.innerHTML += `|NAME|`;
								terminalContent.innerHTML += `<span class="ts">|LANGUAGE|</span><span class="td">|DESCRIPTION|</span>`;
								terminalContent.innerHTML += "<br>";
								if (terminalAttached) terminal.scrollTop = terminal.scrollHeight;

								let counter = 0;
								apps.forEach(project => {
									
									setTimeout( () => {
									
										terminalContent.innerHTML += `[${project.name}]`;
										terminalContent.innerHTML += `<span class="ts">${project.language}</span><span class="td">${project.description}</span>`;
										terminalContent.innerHTML += "<br>";
										
									}, counter);
									
									counter += 500;

								});

								terminalContent.innerHTML += "<br>";
								if (terminalAttached) terminal.scrollTop = terminal.scrollHeight;
								resolve();

							} else {

								setTimeout( () => {
									terminalContent.innerHTML += "An error occurs when trying to list the applications. It seems like your disc is corrupted. <br> <br>";
									if (terminalAttached) terminal.scrollTop = terminal.scrollHeight;
									resolve();
								}, 500);
							};
						}, 500)
					);

				break;
			}

			case "echo":
				history.push( writed )
				terminalContent.innerHTML += writed.slice(4) + "<br>"
				break;

			case "history":
				history.push( writed.toLocaleUpperCase() )

				history.forEach( cmd => terminalContent.innerHTML += cmd + "<br>" );
				break;
			
			default:
				terminalContent.innerHTML += `"${writed}"` + " is not recognized as an internal or external command," + "<br>" +
				"operable program or batch file." + "<br>" + "<br>";
				break;
		};
		
		return 0;
	};

</script>


<div class="terminal abs">
	<span class="top rel">
		<span class="tab abs"> Command Prompt
			<span class="icon">
				<svg width="12px" height="12px" viewBox="-0.5 0 25 25" fill="787878" xmlns="http://www.w3.org/2000/svg">
					<path d="M3 21.32L21 3.32001" stroke="#787878" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
					<path d="M3 3.32001L21 21.32" stroke="#787878" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
				</svg>
			</span>
		</span>
		<span class="icon">
			<svg fill="#787878" width="16px" height="16px" viewBox="0 0 1920 1920" xmlns="http://www.w3.org/2000/svg">
				<path  d="M451.765 1016.47h1016.47V903.53H451.765z"/>
			</svg>
		</span>
		<span class="icon">
			<svg width="16px" height="16px" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
				<path fill-rule="evenodd" clip-rule="evenodd" d="M18 6H6V18H18V6ZM4.5 4.5V19.5H19.5V4.5H4.5Z" fill="#787878"/>
			</svg>
		</span>
		<span class="icon">
			<svg width="16px" height="16px" viewBox="-0.5 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
				<path d="M3 21.32L21 3.32001" stroke="#787878" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
				<path d="M3 3.32001L21 21.32" stroke="#787878" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
			</svg>
		</span>
	</span>
	<span class="body rel" id="terminal-body">
		<input class="input abs" id="input" type="text" on:keydown={ manageKey }/>
		<span id="terminal-body-content"></span>
		{terminalWriting}
		{blinkingBar}
	</span>
</div>

