		z-index: 5000;

		display: flex;
		flex-direction: column;
		width: 655px;
		height: 465px;
		background-color: rgb(50, 50, 50);
		border-radius: 2px 2px 0px 0px;
		border: 2px solid rgb(50, 50, 50);
		border-top: 0px;
		box-shadow: 7px 7px 20px 3px rgba(0, 0, 0, 0.4);
		transform: skewY(30deg) translateX(221px) translateY(-285px);
		scale: 0.22;

		animation: terminal ease;
        animation-fill-mode: forwards;
		animation-timeline: scroll(root);
		animation-range: 5000px 5500px;
	}

	@keyframes terminal {
		100% {
			transform: skewY(0deg)  translateX(0px) translateY(0px);
			width: 100vw;
			height: 100vh;
			scale: 1;
			box-shadow: 7px 7px 20px 3px rgba(0, 0, 0, 0.4);
		}
	}

	.terminal > .body {

		color: white;
		box-sizing: border-box ;
		width: 100%;
