		overflow: hidden;
		overflow-y: scroll;
		overflow-wrap: break-word;
		scroll-padding: 50px;
		scrollbar-width: thin;
		scrollbar-color: transparent transparent;
		padding-bottom: 3rem;
	}
	.terminal > .body::-webkit-scrollbar {
    	width: 6px;
	}

	.terminal > .body::-webkit-scrollbar {
